import React  from 'react';
import { ReactSVG } from 'react-svg';

class SocialBar extends React.Component
{
    addClasses = (svg, classes = []) => {
        for (let i=0;i<classes.length;i++)
            svg.classList.add(classes[i]);
    }

    render()
    {
        let classes = ["social-icons"];
        if (this.props.mobile)
            classes.push("mobile");
        return (
            <ul className={`social-bar ${this.props.mobile ? "mobile": ""}`}>
                    <li>
                        <a className="jumper" href="https://www.instagram.com/johnittofrancis/" target="blank">
                            <ReactSVG beforeInjection={svg => this.addClasses(svg, classes)} src={require("../svgs/instagram.svg")} />
                        </a>
                    </li>
                    <li>
                        <a href="https://www.linkedin.com/in/johnitto-francis-030861157/" target="blank">
                            <ReactSVG beforeInjection={svg => this.addClasses(svg, classes)} src={require("../svgs/linkedin.svg")} />
                        </a>
                    </li>
                    <li>
                        <a href="https://github.com/johnittofrancis1" target="blank">
                            <ReactSVG beforeInjection={svg => this.addClasses(svg, classes)} src={require("../svgs/github.svg")} />
                        </a>
                    </li>
                    {
                        (! this.props.mobile) && <li style={{bottom: '0vh', position: 'fixed', left: '5vw'}} className="vertical-line"></li>
                    }
                </ul>
        );
    }
}

export default SocialBar;